        blockSliderWrapper = block.querySelector(".block__slider-wrapper"),
        teaser = block.querySelectorAll(".teaser-calculate-cost:not(.swiper-slide-duplicate)"),
        arrowPrev = block.querySelector(".arrow-prev"),
        arrowNext = block.querySelector(".arrow-next"),
        blockFooter = block.querySelector(".block__footer"),
        dots = block.querySelector(".block__dots");

    let swiper = "";
    function paramsSlider(slidesPerView, slidesPerGroup, loop, spaceBetween) {
        var options = {
            init: false,
            wrapperClass: "block__slider-wrapper",
            slideClass: "teaser-calculate-cost",
            slidesPerView: slidesPerView,
            slidesPerGroup: slidesPerGroup,
            setWrapperSize: true,
            grabCursor: true,
            spaceBetween: spaceBetween,
            watchOverflow: true,
            updateOnWindowResize: true,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: loop,
            loopFillGroupWithBlank: true,
            freeMode: false,
            freeModeMomentum: false,
            navigation: {
                prevEl: arrowPrev ? arrowPrev : null,
                nextEl: arrowNext ? arrowNext : null,
                disabledClass: "arrows__arrow_disabled",
            },
            pagination: {
                el: dots,
                dynamicBullets: true,
                clickable: true,
            }
        };
        swiper = new Swiper(blockSlider, options);
        swiper.init();
        // arrows ? arrows.style.opacity = "1" : null;
        // arrows ? arrows.style.visibility = "visible" : null;
    }

    // 1025 - ...
    if ( document.body.offsetWidth >= 1025 ) {
        if ( teaser.length > 3 ) {
            if ( !blockSlider.classList.contains(".swiper-container-initialized") ) {
                paramsSlider(
                    slidesPerView = 3,
                    slidesPerGroup = 3,
                    loop = false,
                    spaceBetween = 24,
                );
                dots ? dots.style.display = "block" : null;
                block.classList.add("js-processed-slider");
            }
        } else {
            arrows ? arrows.style.opacity = "0" : null;
            arrows ? arrows.style.visibility = "hidden" : null;
            dots ? dots.style.display = "none" : null;
        }
    }

